import React from "react"
import { Link } from "gatsby"
import styled from "styled-components/macro";
import { rhythm, scale } from "../utils/typography"

const MOBILE_WIDTH = 600;
const MOBILE_QUERY = `(max-width: ${MOBILE_WIDTH}px)`;

const TitleH1 = styled.h1`
  margin-bottom: ${rhythm(1.5)};
  margin-top: 0;
  @media ${MOBILE_QUERY} {
    text-align: center;
  }
`;

const messages = {
  en: {
    title: `Sergey Ulyashev's Blog`,
  },
  ru: {
    title: `  `,
  }
}

class Layout extends React.Component<any> {
  render() {
    const { location, children, langKey = 'en' } = this.props
    const title = messages[langKey].title;
    const homeLink = langKey === 'en' ? '/' : `/${langKey}`;
    let header

    if (location.pathname === homeLink) {
      header = (
        <TitleH1>
          <Link
            style={{
              boxShadow: `none`,
              textDecoration: `none`,
              color: `inherit`,
            }}
            to={homeLink}
          >
            {title}
          </Link>
        </TitleH1>
      )
    } else {
      header = (
        <h3
          style={{
            fontFamily: `Montserrat, sans-serif`,
            marginTop: 0,
          }}
        >
          <Link
            style={{
              boxShadow: `none`,
              textDecoration: `none`,
              color: `inherit`,
            }}
            to={homeLink}
          >
            {title}
          </Link>
        </h3>
      )
    }
    return (
      <div
        style={{
          marginLeft: `auto`,
          marginRight: `auto`,
          maxWidth: rhythm(24),
          padding: `${rhythm(1.5)} ${rhythm(3 / 4)}`,
        }}
      >
        <header>{header}</header>
        <main>{children}</main>
        <footer>
          {new Date().getFullYear()}  Sergey Ulyashev
        </footer>
      </div>
    )
  }
}

export default Layout
